import axios from 'axios'

const api = axios.create({
  baseURL: `http://localhost:3000`,
  withCredentials: true, // the default is false
  headers: {
    Accept: 'application/json',
    'Content-Type': 'application/json',
    'Access-Control-Allow-Credentials': true,
    'Access-Control-Allow-Origin': '*',
  },
  timeout: 10000
})

const upload = axios.create({
  baseURL: `http://localhost:3000`,
  withCredentials: true, // the default is false
  headers: {
    Accept: 'application/json',
    'Content-Type': 'multipart/form-data',
    'Access-Control-Allow-Credentials': true,
    'Access-Control-Allow-Origin': '*',
  },
  timeout: 10000
})


export default {
  // POST api
  getAllPosts (){
    return api.get(`/api/post`);
  },

  createNewPost (payload){
    return upload.post(`/api/post`, payload);
  },

  getPostByID (id, payload){
    return api.post(`/api/post/${id}`, payload);
  },

  updatePost (id, payload){
    return api.put(`/api/post/${id}`, payload);
  },

  deletePost (id){
    return api.delete(`/api/post/${id}`);
  },


  // COMMENT api
  createComment (id, payload){
    return api.post(`/api/post/${id}/comment`, payload);
  },

  getOneComment (id, cm_id){
    return api.get(`/api/post/${id}/comment/${cm_id}/edit`);
  },

  updateComment (id, cm_id, payload){
    return api.post(`/api/post/${id}/comment/${cm_id}`, payload);
  },

  deleteComment (id, cm_id){
    return api.delete(`/api/post/${id}/comment/${cm_id}`);
  },


  // REACT api
  createReact (id, payload){
    return api.post(`/api/post/${id}/react`, payload);
  },

  updateReact (id, cm_id, payload){
    return api.post(`/api/post/${id}/react/${cm_id}`, payload);
  },

  deleteReact (id, rt_id){
    return api.delete(`/api/post/${id}/react/${rt_id}`);
  },


  // AUTH api
  isLoggedIn (){
    return api.post(`/isloggedin`);
  },

  register (payload){
    return api.post(`/register`, payload);
  },

  logIn (payload){
    return api.post(`/login`, payload);
  },

  logOut (){
    return api.get(`/logout`);
  },

  changePassword (payload){
    return api.post(`/change-password`, payload);
  },

  resetPassword (payload){
    return api.post(`/reset-password`, payload);
  },

}
