<template>
  <div id="app">
    
    <Base-Navbar />

    <div class="container" style="padding:0">
      <div class="row"> <!-- justify-content-center -->
        <div class="col" style="padding:0">
          
          <div class="wrapper">
            <router-view :key="$route.fullPath" />
          </div>
          
        </div>
      </div>
    </div>

    <Base-Notification-Modal />
  
  </div>
</template>

<script>
import utils from '@/utils.js';
import NProgress from 'nprogress'
import EventService from '@/services/EventService.js';

export default {
  data (){
    return {
    
    }
  },

  methods: {
    async checkLoginStatus (){
      await this.callServices();
    },

    callServices (){
      NProgress.start()
      return EventService.isLoggedIn()
        .then (res => {
          NProgress.done();
          this.$store.dispatch('postStore/saveUser', res.data[0]);
        })
        .catch (err => {
          this.$store.dispatch('notificationStore/showNotification', {catagory: 'error', title: 'Connection Problem', message: `You are offline.` });
          NProgress.done();
        })
    },
  },

  mounted (){
    this.checkLoginStatus();
    
    // this will add margin-top same as nav height
    document.querySelector('.wrapper').style.marginTop = `${ document.querySelector('nav').scrollHeight }px`;
  },

  components: {
    
  }
}
</script>

