import EventService from '@/services/EventService.js';

export const namespaced = true

export const state = {
  posts: [],
  post: {},
  user: undefined,
}

export const mutations = {
  SET_POSTS (state, posts){
    state.posts = posts;
  },

  SET_POST (state, post){
    state.post = post;
  },

  SET_USER (state, user){
    state.user = user;
  },
}

export const actions = {
  getAllPosts ({commit}){
    return EventService.getAllPosts()
            .then (res => {
              commit('SET_POSTS', res.data[0])
              commit('SET_USER', res.data[1])
            })
            .catch (err => {
              console.log(err)
            })
  },

  addNewPost ({commit, state}, newPost){
    commit('SET_POSTS', [newPost, state.posts])
  },

  updatePosts ({state}, {post, index}){
    state.posts.splice(index, 1, post)
  },

  deletePostsReact ({state}, {postIndex, reactIndex} ){
    delete state.posts[postIndex].reacts[reactIndex]
  },

  getPostByID ({commit}, post_id){
    return EventService.getPostByID(post_id)
            .then (res => {
              commit('SET_POST', res.data[0])
              commit('SET_USER', res.data[1])
            })
            .catch (err => {
              console.log(err)
            })
  },

  saveUser ({commit}, user){
    commit('SET_USER', user)
  },

  forgetUser ({commit}){
    commit('SET_USER', undefined)
  },
}

export const getters = {
  getProductById: state => id => {
      return state.products.find(product => product.id === id)
  }
}